body {
  font-family: sans-serif;
}

#app {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.block {
  min-width: 400px;
  border: 1px solid black;
  padding: 8px;
  margin-top: 8px;
  border-radius: 4px;
}

.id-tag {
  border-radius: 4px;
  padding: 8px;
  color: #fff;
  margin-top: 6px;
}

.gtm-block {
  background-color: #4284f3;
}

.ga-block {
  background-color: #f9aa00;
}

.detecter-block {
  padding: 10px;
}

.detect-btn {
  margin-top: 8px;
}

.article-block {
  max-width: 800px;
  width: 100%;
}

pre {
  background-color: #f0f0f0;
  font-family: "Courier New", monospace;
}

.code-block {
  position: relative;
}

.copy-btn {
  top: 10px;
  right: 10px;
  position: absolute;
  height: 30px;
  width: 30px;
  cursor: pointer;
}
